.card {
    display: flex;
    /* border: 2px solid #333; */
    width: 200px;
    margin: 20px auto;
    padding: 20px;
    border-radius: 20px;
    flex-direction: row;
    justify-content: center;
    box-shadow: 1px 2px 1px 1px rgba(0, 0, 0, .5);
    vertical-align: middle;
}

.card > img {
    margin-right: 20px;
}

img {
    max-width: 5rem;
    height: 5rem;
}